<template>
  <div id="record-section-more">
    <header>
      <img src="../../assets/imgs/images/pre.png" @click="goback" />
      <p>分类</p>
    </header>
    <div class="items">
      <div class="item" v-for="item in listData" :key="item.parent_classify_id">
        <h3>{{ item.parent_classify_name }}</h3>
        <span v-for="it in item.data" :key="it.id">
          <span @click="routerPage(it.id,it.poem_classify_name)"> {{ it.poem_classify_name }} </span>
          <span class="line">|</span>
        </span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "recordSectionMore",
  data() {
    return {
      listData: [],
    };
  },
  created() {
   this.$nextTick(()=>{
     this.getListData();
   })
  },
  methods: {
    goback() {
      this.$router.back(-1);
    },
    async getListData() {
      let data = await this.$axios({
        method: "post",
        url: "https://app.thepoemforyou.com/api/v1/getListByClassify",
      });
      this.listData = data.data.data;
    },
    routerPage(id,title) {
        this.$router.push({
            path:"/record-section-poem",
            query:{
                id,
                title
            }
        })
    }
  },
};
</script>

<style lang="scss" scoped>
@font-face {
  font-family: "fzqk";
  src: url("../../assets/imgs/fzqk.TTF");
  font-family: "dcloud_iconfont";
  src: url("../../assets/fonts/dcloud_iconfont.ttf");
}
#record-section-more {
  header {
    position: sticky;
    top: 0;
    left: 0;
    padding: 10px 20px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    background-color: #fff;
    border-bottom: 1px solid var(--theme-gray);
    img {
      width: 10px;
    }
    p {
      width: 100%;
      font-size: 15px;
      text-align: center;
    }
  }
  .items {
    .item {
      padding: 20px;
      border-bottom: 5px solid var(--theme-french-gray);
      h3 {
        font-weight: bold;
        font-size: 20px;
        margin-bottom: 14px;
        font-family: fzqk;
      }
      span {
        font-family: fzqk;
        font-size: 15px;
        line-height: 1.7em;
        .line {
          font-weight: bold;
          color: var(--theme-green);
        }
      }
    }
  }
}
</style>